<template>
  <div>
    <!-- <div @click="rout('/about')">→</div> -->
    <div class="video_block">
      <video ref="video" @click="togglePlay" src="./assets/video.mp4"></video>
      <img v-if="play == true" @click="togglePlay" class="video_block_play" src="./assets/play.svg" alt="">
    </div>
    <div class="btn">
    <button @click="setPlaybackRate">Скорость {{VideoSpeed}}x</button>
    <button @click="sendStartCommand">Send /start</button>
    </div>
  </div>

</template>
<style>
.btn {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
video {
  border-radius: 50%;
  width: 100%;
}
.video_block {
  position: relative;
  width: 70%;
  padding: 10px;
  margin: 0 auto;
}
.video_block_play {
  position: absolute;
  left: calc(50% - 50px);
  top: calc(50% - 50px);;
  width: 100px;
  height: 100px;
  opacity: 0.5;
  cursor: pointer;
}
</style>
<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

export default {
  data(){
    return{
      title: this.$store.state.token,
      play: true,
      VideoSpeed: 1.0,      
    }
  },
  methods: {
    async sendStartCommand() {
      let tg = window.Telegram.WebApp
      tg.sendData('/start')
    },
    test() {
      this.Jena = "Все равно Юля, никакой мексин на это не повлияет!",
      this.$store.state.token = "Все равно Юля, никакой мексин на это не повлияет!"
    },
    togglePlay() {
      const video = this.$refs.video;
      if (video.paused) {
        video.play();
        this.play = false
      } else {
        video.pause();
        this.play = true
      }
    },
    setPlaybackRate() {
      const video = this.$refs.video;
      if (this.VideoSpeed == 1.0) {
        this.VideoSpeed = 1.5
      } else
      if (this.VideoSpeed == 1.5) {
        this.VideoSpeed = 2.0
      } else 
      if (this.VideoSpeed == 2.0) {
        this.VideoSpeed = 1.0
      }
      video.playbackRate = this.VideoSpeed;
    },
  }
}
</script>
